import React, {createContext, useState} from "react";
import {AppDetails, UUID} from "../types/internal-types";
import {useSessionStorage} from "../hooks/useSessionStorage";
import {SessionStorageGroups} from "./SessionStorageProvider";

type TPinnedApp = Pick<AppDetails, "uuid" | "name">;

export interface PinnedAppsContextProps {
  pinnedAppsList: TPinnedApp[];
  pinnApp: (appDetails: TPinnedApp) => void;
  getPinnedApp: (uuid: UUID) => TPinnedApp | undefined;
  removeApp: (uuid: UUID) => void;
}

export const PinnedAppsContext = createContext<PinnedAppsContextProps>({
  pinnedAppsList: [],
  pinnApp: () => {},
  getPinnedApp: () => ({name: "", uuid: ""}),
  removeApp: () => {}
});

export const PinnedAppsContextProvider: React.FC = ({children}) => {
  const {
    saveToStorage,
    removeFromStorage,
    getStorageItem,
    getAllItems
  } = useSessionStorage();
  const generatePinnedAppList = (): TPinnedApp[] => {
    const pinnedAppsFromStorage = getAllItems(SessionStorageGroups.PinnedApps);
    return Object.keys(pinnedAppsFromStorage).map((pinnedAppId) => {
      return pinnedAppsFromStorage[pinnedAppId];
    });
  };
  const [pinnedAppsList, setPinnedListApps] = useState<TPinnedApp[]>(
    generatePinnedAppList()
  );

  const pinnApp = (appDetails: TPinnedApp) => {
    if (getPinnedApp(appDetails.uuid)) {
      return;
    }
    setPinnedListApps([...pinnedAppsList, appDetails]);
    saveToStorage(SessionStorageGroups.PinnedApps, {
      unique: appDetails.uuid,
      payload: appDetails
    });
  };

  const removeApp = (uuidToRemove: AppDetails["uuid"]) => {
    removeFromStorage(SessionStorageGroups.PinnedApps, uuidToRemove);
    setPinnedListApps(generatePinnedAppList());
  };

  const getPinnedApp = (uuid: AppDetails["uuid"]) => {
    return getStorageItem(SessionStorageGroups.PinnedApps, uuid);
  };

  return (
    <PinnedAppsContext.Provider
      value={{
        pinnedAppsList,
        pinnApp,
        getPinnedApp,
        removeApp
      }}
    >
      {children}
    </PinnedAppsContext.Provider>
  );
};
