import * as React from "react";
import css from "./Applications.module.scss";
import update from "immutability-helper";
import {useFetch} from "../../hooks/useFetch";
import {useServerApiContext} from "../../hooks/useServerApiContext";
import {PageTitle} from "../../components/PageTitle/PageTitle";
import {ApplicationTile} from "./ApplicationTile/ApplicationTile";
import {Flipper} from "react-flip-toolkit";
import {RearrangeAppTiles} from "./RearrangeButton/RearrangeButton";
import {ChangeViewModes} from "./ViewButton/ViewButton";
import {NodeList} from "../../types/internal-types";
import {AppsPageFilters} from "../../enums";
import {useSessionStorage} from "../../hooks/useSessionStorage";
import {SessionStorageGroups} from "../../Providers/SessionStorageProvider";

enum AppsPageStorageItems {
  Filters = "filters",
  ApplicationList = "applicationList",
}

export const DRAG_DROP_TYPE = "applicationTile";

const ViewFilterFucntions = {
  [AppsPageFilters.Available]: (nodeList: NodeList[]) =>
    nodeList.filter((node) => node.online),
  [AppsPageFilters.All]: (nodeList: NodeList[]) => nodeList,
};

export const ApplicationsPage: React.FC<{toggleSideMenu: () => void}> = ({
  toggleSideMenu,
}) => {
  const {dashboardServerApi} = useServerApiContext();

  const [isLoading, nodeList] = useFetch<NodeList[]>(
    dashboardServerApi.getNodeList
  );
  React.useEffect(() => {
    setOrderedNodeList(nodeList);
  }, [isLoading, nodeList]);

  const {saveToStorage, getStorageItem} = useSessionStorage();
  const [orderedNodeList, setOrderedNodeList] = React.useState<NodeList[]>(
    getStorageItem(
      SessionStorageGroups.AppPage,
      AppsPageStorageItems.ApplicationList
    ) || []
  );
  const [previousOrderedNodeList, setPreviousOrderedNodeList] = React.useState(
    orderedNodeList
  );

  const [inDNDState, setDNDState] = React.useState(false);
  const [filter, setFilter] = React.useState<AppsPageFilters>(
    getStorageItem(
      SessionStorageGroups.AppPage,
      AppsPageStorageItems.Filters
    ) || AppsPageFilters.All
  );

  React.useEffect(() => {
    saveToStorage(SessionStorageGroups.AppPage, {
      unique: AppsPageStorageItems.Filters,
      payload: filter,
    });
  }, [saveToStorage, filter]);

  React.useEffect(() => {
    saveToStorage(SessionStorageGroups.AppPage, {
      unique: AppsPageStorageItems.ApplicationList,
      payload: orderedNodeList,
    });
  }, [saveToStorage, orderedNodeList]);

  const moveAppTile = React.useCallback(
    (dragIndex: number, hoverIndex: number) => {
      const dragAppTile = orderedNodeList[dragIndex];
      setOrderedNodeList(
        update(orderedNodeList, {
          $splice: [
            [dragIndex, 1],
            [hoverIndex, 0, dragAppTile],
          ],
        })
      );
    },
    [orderedNodeList]
  );

  const doRearrangeClick = () => {
    setDNDState(true);
    setPreviousOrderedNodeList(orderedNodeList);
  };

  const doSaveClick = () => {
    setDNDState(false);
  };

  const doCancelClick = () => {
    setOrderedNodeList(previousOrderedNodeList);
    setDNDState(false);
  };

  return (
    <div>
      <PageTitle
        toggleSideMenu={toggleSideMenu}
        title="APPLICATIONS"
        actions={[
          <RearrangeAppTiles
            inDNDState={inDNDState}
            doRearrangeClick={doRearrangeClick}
            doSaveClick={doSaveClick}
            doCancelClick={doCancelClick}
          />,
          <ChangeViewModes
            inDNDState={inDNDState}
            setFilter={setFilter}
            filter={filter}
          />,
        ]}
      />
      <Flipper flipKey={JSON.stringify(orderedNodeList.map((s: any) => s.id))}>
        <div className={css.pageContentWrapper}>
          {isLoading ? (
            <span>should be a skeleton here</span>
          ) : (
            ViewFilterFucntions[filter](
              orderedNodeList
            ).map((nodeProps: any, index: number) => (
              <ApplicationTile
                moveAppTile={moveAppTile}
                key={nodeProps.id}
                index={index}
                inDNDState={inDNDState}
                {...nodeProps}
              />
            ))
          )}
        </div>
      </Flipper>
    </div>
  );
};
