import React, {createContext, useState, Dispatch, SetStateAction} from "react";
import {DashboardPages} from "../enums";

export const DashboardPageNavigation = createContext({
  currentDashboardPage: DashboardPages.ApplicationPage,
  selectDashboardPage: (() => {}) as Dispatch<SetStateAction<DashboardPages>>
});

export const DashboardPageNavigationProvider: React.FC = ({children}) => {
  const [currentDashboardPage, setCurrentDashboardPage] = useState(
    DashboardPages.ApplicationPage
  );

  return (
    <DashboardPageNavigation.Provider
      value={{
        currentDashboardPage,
        selectDashboardPage: setCurrentDashboardPage
      }}
    >
      {children}
    </DashboardPageNavigation.Provider>
  );
};
