import React from "react";
import css from "./PageTitle.module.scss";
import {Text} from "../Text";
import classNames from "classnames";
export interface PageTitleProps {
  doAction: () => void;
  icon?: JSX.Element;
  text: string;
  actionActive?: boolean;
}
export const PageTitleAction: React.FC<PageTitleProps> = ({
  doAction,
  icon,
  text,
  actionActive
}) => {
  return (
    <div
      className={classNames(css.actionItem, {[css.active]: actionActive})}
      onClick={doAction}
    >
      {icon && <div className={css.iconWrapper}>{icon}</div>}
      <Text weight="bold" className={css.actionItemText}>
        {text}
      </Text>
    </div>
  );
};
