import * as React from "react";
import css from "./RearrangeButton.module.scss";
import {Text} from "../../../components/Text";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faTimes,
  faArrowsAltV
} from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import {CSSTransition} from "react-transition-group";
import {PageTitleAction} from "../../../components/PageTitle/PageTitleAction";

interface RearrangeAppTilesProps {
  inDNDState: boolean;
  doRearrangeClick: () => void;
  doSaveClick: () => void;
  doCancelClick: () => void;
}

export const RearrangeAppTiles: React.FC<RearrangeAppTilesProps> = ({
  inDNDState,
  doRearrangeClick,
  doSaveClick,
  doCancelClick
}) => {
  return (
    <CSSTransition
      in={inDNDState}
      timeout={200}
      classNames={{
        enterDone: css.enterDone,
        exitDone: css.inDndStateTransitionExitDone
      }}
    >
      <div className={css.rearrangeButtonWrapper}>
        <div className={css.rearrangeButtonWrapperInnter}>
          <PageTitleAction
            doAction={doRearrangeClick}
            text="REARRANGE"
            actionActive={inDNDState}
            icon={
              <FontAwesomeIcon
                icon={faArrowsAltV}
                className={css.faArrowsAltV}
              />
            }
          />
          <CSSTransition
            in={inDNDState}
            timeout={300}
            mountOnEnter
            unmountOnExit
            classNames={{
              appear: css.appear,
              enterDone: css.saveCancelAppsOrderEnterDone
            }}
          >
            <span className={css.saveCancelAppsOrder}>
              <span className={css.saveDNDState} onClick={doSaveClick}>
                <FontAwesomeIcon
                  icon={faCheck}
                  className={classNames(css.saveIcon)}
                />
                <Text fontSize="12px" weight="bold">
                  save
                </Text>
              </span>
              <span className={css.cancelDNDState} onClick={doCancelClick}>
                <FontAwesomeIcon icon={faTimes} className={css.cancelIcon} />
                <Text fontSize="12px" weight="bold">
                  cancel
                </Text>
              </span>
            </span>
          </CSSTransition>
        </div>
      </div>
    </CSSTransition>
  );
};
