import {useEffect, useState} from "react";

export const useFetch = <DataType>(
  fetcherFunction: Function
): [boolean, DataType] => {
  const [data, setData] = useState([] as unknown);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      let data;
      if (typeof fetcherFunction === "function") {
        data = await fetcherFunction();
      } else {
        throw new Error("Please provide a fetcher fucntion");
      }
      setData(data);
      setIsLoading(false);
    };

    fetchData();
  }, [fetcherFunction]);

  return [isLoading, data as DataType];
};
