import * as React from "react";
import css from "../SideMenu.module.scss";
import classNames from "classnames";
import {DashboardPages} from "../../../enums";
import {useDashboardPageNavigation} from "../../../hooks/useDashboardPageNavigation";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
  faGripHorizontal,
  faLaptopCode,
  faProjectDiagram,
  faHeadset
} from "@fortawesome/free-solid-svg-icons";
interface SideMenuItem {
  pageName: DashboardPages;
  children: React.ReactNode[];
  closeSideMenu: () => void;
}

export const GeneralSection: React.FC<{closeSideMenu: () => void}> = ({closeSideMenu}) => (
  <section className={css.generalSectionWrapper}>
    <div className={css.sideMenuTitle}>General</div>
    <SideMenuItem closeSideMenu={closeSideMenu} pageName={DashboardPages.DashboardPage}>
      <FontAwesomeIcon
        icon={faLaptopCode}
        size="2x"
        className={css.sideMenuItemIcon}
      />
      <span className={css.menuItemText}>Dashboard</span>
    </SideMenuItem>
    <SideMenuItem closeSideMenu={closeSideMenu} pageName={DashboardPages.ApplicationPage}>
      <FontAwesomeIcon
        icon={faGripHorizontal}
        size="3x"
        className={css.sideMenuItemIcon}
      />
      <span className={css.menuItemText}>Applications</span>
    </SideMenuItem>
    <SideMenuItem closeSideMenu={closeSideMenu} pageName={DashboardPages.SettingsPage}>
      <FontAwesomeIcon
        icon={faProjectDiagram}
        size="2x"
        className={css.sideMenuItemIcon}
      />
      <span className={css.menuItemText}>Devices</span>
    </SideMenuItem>
    <SideMenuItem closeSideMenu={closeSideMenu} pageName={DashboardPages.HelpPage}>
      <FontAwesomeIcon
        icon={faHeadset}
        size="2x"
        className={css.sideMenuItemIcon}
      />
      <span className={css.menuItemText}>Help</span>
    </SideMenuItem>
  </section>
);

const SideMenuItem = ({children, pageName ,closeSideMenu}: SideMenuItem) => {
  const {
    currentDashboardPage,
    selectDashboardPage
  } = useDashboardPageNavigation();
  return (
    <span
      onClick={() => {
        selectDashboardPage(pageName)
        closeSideMenu();
      }
    }
      className={classNames(css.sideMenuItem, {
        [css.selected]: currentDashboardPage === pageName
      })}
    >
      {<>{children}</>}
    </span>
  );
};
