import * as React from "react";
import css from "./ApplicationTile.module.scss";
import classNames from "classnames";
import {Text} from "../../../components/Text";
import {Flipped} from "react-flip-toolkit";
import {useApplicationDND} from "../../../hooks/useApplicationDND";
import {UUID} from "../../../types/internal-types";
import {Loader} from "../../../components/Loader/Loader";
import {Thumbnail} from "../../../components/Thumbnail/Thumbnail";
import {useServerApiContext} from "../../../hooks/useServerApiContext";
import {useFocusedApplication} from "../../../hooks/useFocusedApplication";
import {useDashboardPageNavigation} from "../../../hooks/useDashboardPageNavigation";
import {DashboardPages} from "../../../enums";
import {TileOptionsMenu} from "../TileOptionsMenu/TileOptionsMenu";

interface ApplicationTileProps {
  thumbnail?: string;
  name: string;
  description: string;
  index: number;
  id: number;
  uuid: UUID;
  inDNDState: boolean;
  online: boolean;
  moveAppTile: (dragIndex: number, hoverIndex: number) => void;
}

export const ApplicationTile: React.FC<ApplicationTileProps> = ({
  name,
  id,
  uuid,
  index,
  moveAppTile,
  inDNDState,
  online
}) => {
  const ref = React.useRef<HTMLDivElement>(null);
  const [isLoading, setIsLoading] = React.useState(false);
  const {
    dashboardServerApi: {getAppData}
  } = useServerApiContext();
  const {setCurrentFocusedApp} = useFocusedApplication();
  const {
    dragFunc,
    dropFunc,
    collectedProps: {isDragging}
  } = useApplicationDND({
    ref,
    doDrag: moveAppTile,
    dropIndex: index,
    canDrag: inDNDState,
    id
  });
  const {selectDashboardPage} = useDashboardPageNavigation();

  const applicationTileClasses = classNames(css.applicationTile, {
    [css.withDndStyles]: inDNDState,
    [css.dragging]: isDragging,
    [css.appIsLoading]: isLoading,
    [css.notAvailable]: !online
  });

  const openApp = async () => {
    setIsLoading(true);
    const {
      html: [htmlStream],
      scripts: [scriptStream]
    } = await getAppData(uuid);
    setCurrentFocusedApp({
      html: htmlStream,
      script: scriptStream,
      currentAppName: name,
      currentAppId: uuid
    });
    selectDashboardPage(DashboardPages.FocusedApp);
  };

  dragFunc(dropFunc(ref));

  return (
    <div>
      <div className={css.dndPlaceHolder} />
      <Flipped flipId={id}>
        <div className={css.applicationTileOuter}>
          <div
            ref={ref}
            className={applicationTileClasses}
            style={{
              opacity: isDragging ? 0 : 1
            }}
          >
            <div onClick={openApp} className={css.thumbnailWrapper}>
              <Thumbnail name={name} inDndState={inDNDState} />
            </div>
            <TileOptionsMenu
              inDndState={inDNDState}
              isLoading={isLoading}
              appDetails={{
                name,
                uuid
              }}
            />
            <Text
              className={css.applicationName}
              weight="normal"
              fontSize="12px"
            >
              {name}
            </Text>
            <div onClick={openApp} className={css.openApplication}>
              <Text weight="bold" fontSize="12px" color="#768093">
                OPEN
              </Text>
            </div>
            {isLoading && (
              <div className={css.loader}>
                <Loader />
              </div>
            )}
            {!online && <div className={css.notAvailablePlaceholder} />}
          </div>
        </div>
      </Flipped>
    </div>
  );
};
