import {DashboardTile} from "./DashboardTile/DashboardTile";
import {AppLoader} from "./AppLoader";
import {MkAppDescription} from "../../types/internal-types";
import {PageTitle} from "../../components/PageTitle/PageTitle";
import * as React from "react";
import css from "./Dashboard.module.scss";
import {DashboardTileSkeleton} from "../../components/Skeleton/DashboardTile";
import {DashboardContext} from "../../Providers/ServerApiContext";
import {Text} from "../../components/Text";

const innitialDashboardsPageState = {appList: [] as any};

export class DashboardsPage extends React.Component<
  {toggleSideMenu: () => void},
  typeof innitialDashboardsPageState
> {
  readonly state = innitialDashboardsPageState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  public async componentDidMount() {
    const appList = await this.context.dashboardServerApi.getNodeList();
    this.setState({appList});
  }

  render() {
    const {appList} = this.state;
    return (
      <>
        <div className={css.underConstruction}>
          <PageTitle title="DASHBOARD" toggleSideMenu={this.props.toggleSideMenu} />
          <div className={css.pageContentWrapper}>
            {appList &&
              appList.map((mkApp: MkAppDescription, index: any) => (
                <AppLoader
                  key={index}
                  mkAppInitData={mkApp}
                  render={(loadedProps) => {
                    return loadedProps ? (
                      <DashboardTile {...loadedProps} />
                    ) : (
                      <DashboardTileSkeleton />
                    );
                  }}
                />
              ))}
          </div>
        </div>
        <div
          style={{
            display: "block",
            marginTop: "20.7%",
            textAlign: "center",
            zIndex: 10,
            background: "#d6eeff",
            position: "relative",
            boxShadow: "inset 20px 0 30px #fff",
          }}
        >
          <Text weight="bold" fontSize="46px" color="$grey01">
            UNDER CONSTRUCTION
          </Text>
        </div>
      </>
    );
  }
}
