import {PageTitle} from "../../components/PageTitle/PageTitle";
import React, {useEffect} from "react";
import css from "./NodePage.module.scss";
import {useServerApiContext} from "../../hooks/useServerApiContext";
import {AnimationsContext} from "../../Providers/AnimationsContextProvider";
import {NodeList} from "../../types/internal-types";
import {useFetch} from "../../hooks/useFetch";
import {toCamelCase} from "../../utils/CamelCaseConverter";
import {NodeCard} from "./NodeCard/NodeCard";
import {NodePageFilters} from "../../enums";
import {ChangeViewModes} from "../Applications/ViewButton/ViewButton";
import {PageTitleAction} from "../../components/PageTitle/PageTitleAction";
import {useSessionStorage} from "../../hooks/useSessionStorage";
import {SessionStorageGroups} from "../../Providers/SessionStorageProvider";

const ViewFilterFucntions = {
  [NodePageFilters.Available]: (nodeList: NodeList[]) =>
    nodeList.filter((node) => node.online),
  [NodePageFilters.All]: (nodeList: NodeList[]) => nodeList,
  [NodePageFilters.MachinesOnly]: (nodeList: NodeList[]) =>
    nodeList.filter((node) => node.name === "Master"),
};

export interface OwnProps {}

export const NodesPage: React.FC<{toggleSideMenu: () => void}> = ({
  toggleSideMenu,
}) => {
  const pageRef = React.useRef<HTMLDivElement>(null);
  const {dashboardServerApi} = useServerApiContext();
  const [isLoading, nodeList] = useFetch<NodeList[]>(
    dashboardServerApi.getNodeList
  );
  const {getStorageItem, saveToStorage} = useSessionStorage();

  const [filter, setFilter] = React.useState<NodePageFilters>(
    getStorageItem(SessionStorageGroups.DevicesPage, "filters") ||
      NodePageFilters.All
  );
  const [machinesOnly, setMachinesOnly] = React.useState(true);

  useEffect(() => {
    saveToStorage(SessionStorageGroups.DevicesPage, {
      unique: "filters",
      payload: filter,
    });
  }, [saveToStorage, filter]);

  const filterMachinesOnly = (nodeList: NodeList[]) => {
    return machinesOnly
      ? ViewFilterFucntions.MACHINES_ONLY(nodeList)
      : nodeList;
  };

  const filterNodeList = (nodeList: NodeList[]) => {
    return ViewFilterFucntions[filter](nodeList);
  };

  return (
    <AnimationsContext.Provider value={{nodePageRef: pageRef}}>
      <>
        <PageTitle
          toggleSideMenu={toggleSideMenu}
          title={
            <div className={css.pageTitleCustomWrapper}>
              <div className={css.pageTitle}>DEVICES</div>
              <PageTitleAction
                doAction={() => setMachinesOnly(true)}
                text="MACHINES"
                actionActive={machinesOnly}
              />
              <PageTitleAction
                doAction={() => setMachinesOnly(false)}
                text="NODES"
                actionActive={!machinesOnly}
              />
            </div>
          }
          actions={<ChangeViewModes setFilter={setFilter} filter={filter} />}
        />
        <div ref={pageRef} className={css.pageContentWrapper}>
          {filterMachinesOnly(filterNodeList(nodeList)).map((node, index) => (
            <div key={index} className={css.nodeCardWrapper}>
              <NodeCard nodeDetails={toCamelCase(node)} isLoading={isLoading} />
            </div>
          ))}
        </div>
      </>
    </AnimationsContext.Provider>
  );
};
