import * as React from "react";
import css from "./Thumbnail.module.scss";
import classNamesHelper from "classnames";
import {Text} from "../Text";

interface ThumbnailProps {
  name: string;
  inDndState?: boolean;
  size?: TSizes;
  className?: string;
}

type TSizes = "x1" | "x2";

export const Thumbnail: React.FC<ThumbnailProps> = ({
  name,
  inDndState = false,
  size = "x2",
  className
}) => {
  let classes = "";
  let fontSize = "";

  switch (size) {
    case "x1":
      classes = css.smallWrapper;
      fontSize = "10px";
      break;
    case "x2":
      classes = css.normalWrapper;
      fontSize = "16px";
      break;
  }

  return (
    <div
      className={classNamesHelper(classes, className, {
        [css.withDndStyles]: inDndState
      })}
    >
      <div className={css.thumbnailText}>
        <Text weight="bold" fontSize={fontSize}>
          {`${name.charAt(0).toUpperCase()}${name.charAt(1).toUpperCase()}`}
        </Text>
      </div>
    </div>
  );
};
