import React from "react";
import css from "./PageTitle.module.scss";
import classNames from "classnames";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars} from "@fortawesome/free-solid-svg-icons";
import { MobileView } from "react-device-detect";

type TThemes = "thin";
export interface PageTitleProps {
  title: JSX.Element | string;
  toggleSideMenu?: () => void;
  actions?: JSX.Element[] | JSX.Element;
  theme?: TThemes;
  className?: {[s: string]: string};
}
export const PageTitle: React.FC<PageTitleProps> = ({
  title,
  actions,
  theme,
  toggleSideMenu,
  className,
}) => (
  <header
    className={classNames(css.pageTitle, {
      [css.thin]: theme === "thin",
      ...className,
    })}
  >
    <MobileView>
      {toggleSideMenu && (
        <section onClick={toggleSideMenu} className={css.sideMenuHamburger}>
          <FontAwesomeIcon icon={faBars} />
        </section>
      )}
    </MobileView>
    <section className={css.pageTitleContent}>
      <div className={css.pageTitleName}>{title}</div>
      {actions && (
        <div className={css.currentAppActions}>
          {React.Children.map(actions, (child) => (
            <>{child}</>
          ))}
        </div>
      )}
    </section>
  </header>
);
