import * as React from "react";
import css from "./FocusedApp.module.scss";
import {Portal} from "../Portal";
import {useFocusedApplication} from "../../hooks/useFocusedApplication";
import {PageTitle} from "../PageTitle/PageTitle";
import {Thumbnail} from "../Thumbnail/Thumbnail";
import {Text} from "../Text";
import {FocusedAppActions} from "./FocusedAppActions";

export const FocusedApp: React.FC<{toggleSideMenu: () => void}> = ({
  toggleSideMenu,
}) => {
  const {focusedAppStreams, currentAppName} = useFocusedApplication();

  return (
    <>
      <PageTitle
        toggleSideMenu={toggleSideMenu}
        title={
          <>
            <Thumbnail
              className={css.currentAppThumbnail}
              name={currentAppName}
              size="x2"
            />
            <Text fontSize="18px" weight="bold">
              {currentAppName}
            </Text>
          </>
        }
        actions={<FocusedAppActions />}
      />
      <Portal
        html={focusedAppStreams!.html}
        scripts={focusedAppStreams!.script}
      ></Portal>
    </>
  );
};
