import React from "react";
import css from "./NodeCard.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars, faCodeBranch, faHdd} from "@fortawesome/free-solid-svg-icons";
import {SkeletonShimmer} from "../../../components/SkeletonShimmer";
import {Subtitle, CardText} from "./NodeCardTypography";

export interface NodeCardSummaryProps {
  isLoading?: boolean;
  onNodeSettingsClick: Function;
  nodeDetails: any;
}

enum DeviceTypes {
  Master = 1
}

export const NodeCardSummary: React.FC<NodeCardSummaryProps> = ({
  isLoading = false,
  onNodeSettingsClick,
  nodeDetails: {name, type, uuid, online: isOnline}
}) => {
  return (
    <div className={css.nodeCardSummaryWrapper}>
      <CardIconSection type={type} />
      <CardSection
        isLoading={isLoading}
        customClassName={css.deviceType}
        description={type}
      >
        DEVICE TYPE
      </CardSection>
      <CardSection
        isLoading={isLoading}
        customClassName={css.deviceName}
        description={name}
      >
        DEVICE NAME
      </CardSection>
      <CardSection
        isLoading={isLoading}
        customClassName={css.deviceId}
        description={uuid}
      >
        DEVICE ID
      </CardSection>
      <CardSection
        isLoading={isLoading}
        customClassName={css.deviceStatus}
        description={isOnline ? "Connected" : "Disconnected"}
      >
        STATUS
      </CardSection>
      <SummaryOptionsMenu onNodeSettingsClick={onNodeSettingsClick} />
      {isLoading && (
        <SkeletonShimmer width="1400px" color="rgba(249, 250, 251, 0.001)" />
      )}
    </div>
  );
};

const CardIconSection: React.FC<{type: number}> = ({type}) => (
  <div className={`${css.section} ${css.IconSection}`}>
    <FontAwesomeIcon
      icon={type === DeviceTypes.Master ? faHdd : faCodeBranch}
      size="2x"
      color="#768093"
    />
  </div>
);

const SummaryOptionsMenu: React.FC<{onNodeSettingsClick: Function}> = ({
  onNodeSettingsClick
}) => (
  <div className={`${css.deviceContentWrapper} ${css.deviceSettingsMenu}`}>
    <span
      className={css.deviceSettingsMenuIcon}
      onClick={() => onNodeSettingsClick()}
    >
      <FontAwesomeIcon icon={faBars} size="2x" color="#768093" />
    </span>
  </div>
);

interface CardSectionProps {
  customClassName: string;
  children: string;
  isLoading: boolean;
  description: string;
}

const CardSection: React.FC<CardSectionProps> = ({
  isLoading,
  customClassName,
  children,
  description
}) => (
  <div
    className={`${css.section} ${css.deviceContentWrapper} ${customClassName}`}
  >
    <Subtitle showSkeleton={isLoading}>{children}</Subtitle>
    <CardText showSkeleton={isLoading}>
      <span className={css.cardSectionText} title={description}>
        {description}
      </span>
    </CardText>
  </div>
);
