import React, {useState, createContext} from "react";
import {AppDetails, UUID} from "../types/internal-types";

export interface AppSteamDetails {
  html: string;
  script?: string;
  styles?: string;
}

interface CurrentFocusedApp extends AppSteamDetails {
  currentAppName: string;
  currentAppId: UUID;
}

interface FocusedApplication {
  focusedAppStreams: AppSteamDetails | null;
  currentAppName: AppDetails["name"];
  setCurrentFocusedApp: (st: CurrentFocusedApp) => void;
  removeFromFocus: () => void;
  currentAppId: UUID;
}

export const FocusedApplicationContext = createContext<FocusedApplication>({
  focusedAppStreams: null,
  currentAppName: "",
  setCurrentFocusedApp: (_st: CurrentFocusedApp) => {},
  removeFromFocus: () => {},
  currentAppId: ""
});

export const FocusedAppContextProvider: React.FC = ({children}) => {
  const [currentAppName, setCurrentAppName] = useState<AppDetails["name"]>("");
  const [currentAppId, setCurrentAppId] = useState<AppDetails["uuid"]>("");
  const [
    focusedAppStreams,
    setAppSteamDetails
  ] = useState<AppSteamDetails | null>(null);

  const setCurrentFocusedApp = ({
    html,
    script,
    currentAppName,
    currentAppId
  }: CurrentFocusedApp) => {
    setAppSteamDetails({html, script});
    setCurrentAppName(currentAppName);
    setCurrentAppId(currentAppId);
  };

  const removeFromFocus = () => {
    setAppSteamDetails(null);
    setCurrentAppName("");
  };
  return (
    <FocusedApplicationContext.Provider
      value={{
        currentAppName,
        setCurrentFocusedApp,
        removeFromFocus,
        focusedAppStreams,
        currentAppId
      }}
    >
      {children}
    </FocusedApplicationContext.Provider>
  );
};
