import React from "react";
import css from "./ViewButton.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSortAmountDown} from "@fortawesome/free-solid-svg-icons";
import {PageTitleAction} from "../../../components/PageTitle/PageTitleAction";
import {CSSTransition} from "react-transition-group";
import {Text} from "../../../components/Text";
import classNames from "classnames";
import {AppsPageFilters} from "../../../enums";

interface ChangeViewModesProps {
  inDNDState?: boolean;
  filter: any;
  setFilter: any;
}

export const ChangeViewModes: React.FC<ChangeViewModesProps> = ({
  filter,
  setFilter
}) => {
  const [isActionMenuOpen, setIsActionMenuOpen] = React.useState(false);

  return (
    <>
      <div className={css.viewFilterWrapper}>
        <PageTitleAction
          doAction={() => setIsActionMenuOpen(!isActionMenuOpen)}
          actionActive={isActionMenuOpen}
          icon={
            <FontAwesomeIcon
              icon={faSortAmountDown}
              className={css.afaSortAmountDown}
            />
          }
          text="VIEW"
        />
        {isActionMenuOpen && (
          <ActionMenu
            setFilter={setFilter}
            filter={filter}
            isActionMenuOpen={isActionMenuOpen}
            setIsActionMenuOpen={setIsActionMenuOpen}
          />
        )}
      </div>
      <div className={css.selectedView}>
        <Text weight="bold">{filter}</Text>
      </div>
    </>
  );
};

interface ActionMenuProps {
  isActionMenuOpen: boolean;
  setIsActionMenuOpen: React.Dispatch<React.SetStateAction<boolean>>;
  setFilter: any;
  filter: any;
}

const ActionMenu: React.FC<ActionMenuProps> = ({
  setFilter,
  isActionMenuOpen,
  setIsActionMenuOpen,
  filter
}) => {
  const ref = React.useRef<HTMLDivElement>(null);

  React.useLayoutEffect(() => {
    document.addEventListener("mousedown", handleClick, false);
    return () => document.removeEventListener("mousedown", handleClick, false);
  });

  const handleClick = (e: any) => {
    if (ref.current && ref.current.contains(e.target)) {
      return;
    }
    setIsActionMenuOpen(false);
  };

  const doActionClick = (selectedFilter: AppsPageFilters) => {
    setFilter(selectedFilter);
    setIsActionMenuOpen(false);
  };
  return (
    <CSSTransition
      in={isActionMenuOpen}
      appear
      classNames={{
        appear: css.appear,
        enterDone: css.enterDone
      }}
      timeout={0}
    >
      <div className={css.actionMenu} ref={ref}>
        <div
          className={classNames(css.actionMenuItem, {
            [css.selected]: filter === AppsPageFilters.All
          })}
          onClick={() => doActionClick(AppsPageFilters.All)}
        >
          {AppsPageFilters.All}
        </div>
        <div
          className={classNames(css.actionMenuItem, {
            [css.selected]: filter === AppsPageFilters.Available
          })}
          onClick={() => doActionClick(AppsPageFilters.Available)}
        >
          {AppsPageFilters.Available}
        </div>
      </div>
    </CSSTransition>
  );
};
