import React from "react";
import css from "./App.module.scss";
import {SideMenu} from "../SideMenu/SideMenu";
import {DashboardsPage} from "../../pages/Dashboard/Dashboard";
import {NodesPage} from "../../pages/Nodes/NodesPage";
import {ApplicationsPage} from "../../pages/Applications/Applications";
import {DashboardPages} from "../../enums";
import {useDashboardPageNavigation} from "../../hooks/useDashboardPageNavigation";
import {FocusedApp} from "../FocusedApp/FocusedApp";
import {CSSTransition} from "react-transition-group";
import classNames from "classnames";
import {
  BrowserView,
  MobileView,
} from "react-device-detect";

window.__DASHBOARD_GLOBALS__ = {
  userSessionId: "1234-1234321-dk322-111",
};
export const App: React.FC = () => {
  const {currentDashboardPage} = useDashboardPageNavigation();
  const [isSideMenuOpened, setIsSideMenuOpened] = React.useState(false);
  const [isMounted, setIsMounted] = React.useState<boolean>(true);
  const [
    currentPageToRender,
    setCurrentPageToRender,
  ] = React.useState<null | DashboardPages>(null);

  React.useEffect(() => {
    setIsMounted(false);

    const timeoutId = setTimeout(() => {
      setCurrentPageToRender(currentDashboardPage);
      setIsMounted(true);
    }, 300);
    return () => clearTimeout(timeoutId);
  }, [currentDashboardPage]);

  const getPage = () => {
    switch (currentPageToRender) {
      case DashboardPages.DashboardPage:
        return (
          <DashboardsPage
            toggleSideMenu={() => setIsSideMenuOpened(!isSideMenuOpened)}
          />
        );
      case DashboardPages.SettingsPage:
        return (
          <NodesPage
            toggleSideMenu={() => setIsSideMenuOpened(!isSideMenuOpened)}
          />
        );
      case DashboardPages.ApplicationPage:
        return (
          <ApplicationsPage
              toggleSideMenu={() => setIsSideMenuOpened(!isSideMenuOpened)}
          />
        );
      case DashboardPages.FocusedApp:
        return (
          <FocusedApp
            toggleSideMenu={() => setIsSideMenuOpened(!isSideMenuOpened)}
          />
        );
      default:
        return (
          <DashboardsPage
            toggleSideMenu={() => setIsSideMenuOpened(!isSideMenuOpened)}
          />
        );
    }
  };

  return (
    <main
      className={classNames(css.siteContent, {
        [css.sideMenuOpened]: isSideMenuOpened,
      })}
    >
      <SideMenu closeSideMenu={() => setIsSideMenuOpened(false)}/>
      <section className={css.pageRightWrapper}>
        <BrowserView viewClassName={css.browserViewWrapper}>
          <div className={css.pageBasePlaceholder} />
          <CSSTransition
          in={isMounted}
          timeout={0}
          classNames={{
            enterDone: css.slideInAnimation,
            exitDone: css.slideOutAnimation,
          }}
          >
            <section className={css.pageWrapper}>{getPage()}</section>
          </CSSTransition>
        </BrowserView>
        <MobileView viewClassName={css.mobileViewWrapper}>
          <section className={css.mobilePageWrapper}>{getPage()}</section> 
        </MobileView>
      </section>
    </main>
  );
};
