import React, {createContext} from "react";

export enum SessionStorageGroups {
  PinnedApps = "pinnedApps",
  AppPage = "appPage",
  DevicesPage = "devicesPage"
}

export const SessionStorageContext = createContext({
  userSessionId: "",
  storageId: ""
});

export const SessionStorageProvider: React.FC<{userSessionId: string}> = ({
  children,
  userSessionId
}) => {
  const innitialStorrageStruct = {
    [SessionStorageGroups.PinnedApps]: {},
    [SessionStorageGroups.AppPage]: {},
    [SessionStorageGroups.DevicesPage]: {}
  };
  const storageId = `user-session-id|${userSessionId}`;
  const hasPreviousStorrageData = !!localStorage.getItem(storageId);

  if (!hasPreviousStorrageData) {
    localStorage.setItem(
      `user-session-id|${userSessionId}`,
      JSON.stringify(innitialStorrageStruct)
    );
  }
  return (
    <SessionStorageContext.Provider
      value={{
        userSessionId,
        storageId
      }}
    >
      {children}
    </SessionStorageContext.Provider>
  );
};
