import * as React from "react";
import css from "./TileOptionsMenu.module.scss";
import classNames from "classnames";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars} from "@fortawesome/free-solid-svg-icons";
import {AppDetails} from "../../../types/internal-types";
import {usePinnedAppsContext} from "../../../hooks/usePinnedApps";
import {PinnAppIcon} from "../../../components/PinnApp/PinnApp";

interface TileOptionsMenuProps {
  inDndState: boolean;
  appDetails: Pick<AppDetails, "name" | "uuid">;
  isLoading: boolean;
}

export const TileOptionsMenu: React.FC<TileOptionsMenuProps> = ({
  inDndState,
  appDetails: {name, uuid},
  isLoading
}) => {
  const {pinnApp, getPinnedApp, removeApp} = usePinnedAppsContext();
  const isPinned = !!getPinnedApp(uuid);

  const togglePinnedApp = () =>
    isPinned ? removeApp(uuid) : pinnApp({name, uuid});
  return (
    <div className={css.optionsMenu}>
      <PinnAppIcon
        doPinnApp={togglePinnedApp}
        isPinned={isPinned}
        externalstyles={classNames({
          [css.appIsLoading]: isLoading,
          [css.optionsMenuDnd]: inDndState
        })}
      />
      <span onClick={() => console.log("bang!")}>
        <FontAwesomeIcon
          className={classNames(css.optionsItem, {
            [css.appIsLoading]: isLoading,
            [css.optionsMenuDnd]: inDndState
          })}
          icon={faBars}
          size="1x"
        />
      </span>
    </div>
  );
};
