import React from "react";
import {useServerApiContext} from "../../../hooks/useServerApiContext";
import css from "./NodeSettings.module.scss";
import {UUID, NodeSettingsData} from "../../../types/internal-types";
import {Portal} from "../../../components/Portal";
import {useFetch} from "../../../hooks/useFetch";
import {faTimes} from "@fortawesome/free-solid-svg-icons";
import {Loader} from "../../../components/Loader/Loader";
import {PageTitle} from "../../../components/PageTitle/PageTitle";
import {PageTitleAction} from "../../../components/PageTitle/PageTitleAction";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

interface NodeSettingsProps {
  deviceUuid: UUID;
  nodeName: string;
  onClose: () => void;
}

export const NodeSettings: React.FC<NodeSettingsProps> = ({
  deviceUuid,
  nodeName,
  onClose,
}) => {
  const {
    dashboardServerApi: {getNodeSettings},
  } = useServerApiContext();

  const fetcherFucntion = React.useCallback(() => getNodeSettings(deviceUuid), [
    deviceUuid,
    getNodeSettings,
  ]);
  const [isLoading, nodeSettingsData] = useFetch<NodeSettingsData>(
    fetcherFucntion
  );

  return (
    <div className={css.nodeSettingsWrapper}>
      <PageTitle
        title={nodeName}
        theme="thin"
        className={{[css.nodeSettingsTitle]: css.nodeSettingsTitle}}
        actions={
          <PageTitleAction
            icon={<FontAwesomeIcon icon={faTimes} className={css.faTimes} />}
            doAction={onClose}
            text="CLOSE"
          />
        }
      />
      <div className={css.nodeSettingsContent}>
        {isLoading ? (
          <Loader />
        ) : (
          <Portal
            scripts={nodeSettingsData.scripts[0]}
            html={nodeSettingsData.html[0]}
          />
        )}
      </div>
    </div>
  );
};
