import {useDrop, DropTargetMonitor, useDrag} from "react-dnd";
import {DRAG_DROP_TYPE} from "../pages/Applications/Applications";

interface DragItem {
  dropIndex: number;
  id: string;
  type: string;
}

interface UseApplicationDNDParams {
  ref: React.RefObject<HTMLDivElement>;
  doDrag: Function;
  dropIndex: number;
  canDrag: boolean;
  id: number;
}

interface CollectedProps {
  isDragging: boolean;
}
type UseApplicationDND = (
  p: UseApplicationDNDParams
) => {dragFunc: any; dropFunc: any; collectedProps: CollectedProps};

export const useApplicationDND: UseApplicationDND = ({
  ref,
  doDrag,
  dropIndex,
  canDrag,
  id
}) => {
  const [, drop] = useDrop({
    accept: DRAG_DROP_TYPE,
    hover(item: DragItem, monitor: DropTargetMonitor) {
      if (!ref.current) {
        return;
      }
      const dragIndex = item.dropIndex;
      const hoverIndex = dropIndex;

      if (dragIndex === hoverIndex) {
        return;
      }

      // Determine Hovered on rectangle position on screen
      const hoverBoundingRect = ref.current!.getBoundingClientRect();

      // Determine mouse position
      const clientOffset = monitor.getClientOffset();
      const hoverMiddleX =
        (hoverBoundingRect.left - hoverBoundingRect.right) / 2;
      const hoverClientX = clientOffset!.x - hoverBoundingRect.right;
      // Dragging rightwards
      if (dragIndex < hoverIndex && hoverClientX < hoverMiddleX) {
        return;
      }

      // Dragging leftwards
      if (dragIndex > hoverIndex && hoverClientX > hoverMiddleX) {
        return;
      }

      doDrag(dragIndex, hoverIndex);
      item.dropIndex = hoverIndex;
    }
  });

  const [collectedProps, drag] = useDrag({
    item: {type: DRAG_DROP_TYPE, id, dropIndex},
    canDrag,
    collect: (monitor: any) => ({
      isDragging: monitor.isDragging()
    })
  });

  return {dragFunc: drag, dropFunc: drop, collectedProps};
};
