import React from "react";
import css from "./NodeCard.module.scss";
import {NodeSettings} from "../NodeSettings/NodeSettings";
import {OpenNodeSettingsAnimation} from "../NodeCardAnimations/OpenNodeSettingsAnimation";
import {NodeCardSummary} from "./NodeCardSummary";
import classNames from "classnames";

export interface NodeCardProps {
  isLoading: boolean;
  selected?: boolean;
  nodeDetails: any;
}

export const NodeCard: React.FC<NodeCardProps> = ({
  isLoading,
  nodeDetails,
  selected,
  nodeDetails: {online: isOnline, name, uuid}
}) => {
  const [cardSettingOpenned, setCardSettingsOpenned] = React.useState(false);
  const openCardSettings = () => setCardSettingsOpenned(true);
  const closeCardSettings = () => setCardSettingsOpenned(false);

  return (
    <OpenNodeSettingsAnimation isOpened={cardSettingOpenned}>
      <div
        className={classNames(css.cardWrapper, {
          [css.hovered]: cardSettingOpenned || selected,
          [css.disabled]: !isOnline
        })}
      >
        {!cardSettingOpenned ? (
          <NodeCardSummary
            nodeDetails={nodeDetails}
            isLoading={isLoading}
            onNodeSettingsClick={openCardSettings}
          />
        ) : (
          <NodeSettings
            nodeName={name}
            onClose={closeCardSettings}
            deviceUuid={uuid}
          />
        )}
      </div>
    </OpenNodeSettingsAnimation>
  );
};
