import {useContext} from "react";
import {
  SessionStorageContext,
  SessionStorageGroups,
} from "../Providers/SessionStorageProvider";

export const useSessionStorage = () => {
  const {storageId} = useContext(SessionStorageContext);

  const saveToStorage = (
    groupName: SessionStorageGroups,
    value: {unique: string; payload: any}
  ) => {
    setOrCreateStorageGroupItem(groupName, value);
  };

  const setOrCreateStorageGroupItem = (
    groupName: SessionStorageGroups,
    {unique, payload}: {unique: string; payload: any}
  ) => {
    let groups = getStorageGroups();
    const group = groups[groupName];

    if (group[unique]) {
      group[unique] = payload;
    } else {
      groups[groupName] = {
        ...group,
        ...{[unique]: payload},
      };
    }
    localStorage.setItem(storageId, JSON.stringify(groups));
  };

  const getStorageItem = (
    groupName: SessionStorageGroups,
    storageGroupItemName: string
  ) => {
    const storage = getStorageGroups();
    return storage[groupName][storageGroupItemName];
  };

  const removeFromStorage = (
    groupName: SessionStorageGroups,
    storageGroupItemName: string
  ) => {
    let storage = getStorageGroups();
    delete storage[groupName][storageGroupItemName];
    localStorage.setItem(storageId, JSON.stringify(storage));
    return getStorageGroups(groupName);
  };

  const getAllItems = (
    groupName: SessionStorageGroups
  ): {[unique: string]: any} => {
    return getStorageGroups(groupName);
  };
  ////////////////////priavte methods///////////////////////

  const getStorageGroups = (item?: string) => {
    const storage = localStorage.getItem(storageId);
    if (!storage) {
      return;
    }

    return item ? JSON.parse(storage)[item] : JSON.parse(storage);
  };

  return {saveToStorage, removeFromStorage, getAllItems, getStorageItem};
};
