import * as React from "react";
import css from "./SideMenu.module.scss";
import {GeneralSection} from "./MenuSections/GeneralSection";
import {PinnedSection} from "./MenuSections/PinnedSection";
import {LoggedInUserSection} from "./MenuSections/LoggedInUserSection";
import classNames from "classnames";
import {Footer} from "./MenuSections/Footer";
interface SideMenuProps {
  closeSideMenu: () => void;
}

export const SideMenu: React.FC<SideMenuProps> = ({closeSideMenu}) => {
  const [isMinimized, setIsMinimized] = React.useState(false);
  const [isClosed, setIsClosed] = React.useState(true);
  const ref = React.useRef<HTMLDivElement>(null);

  React.useLayoutEffect(() => {
    document.addEventListener("mousedown", handleClick, false);
    return () => document.removeEventListener("mousedown", handleClick, false);
  });

  const handleClick = (e: any) => {
    if (ref.current && ref.current.contains(e.target)) {
      return;
    }
    closeSideMenu();
    setIsClosed(true);
  };

  return (
    <nav
      ref={ref}
      className={classNames(css.sideMenuWrapper, {
        [css.minimizedView]: isMinimized,
        [css.closed]: isClosed,
      })}
    >
      <LoggedInUserSection closeSideMenu={closeSideMenu} />
      <GeneralSection closeSideMenu={closeSideMenu} />
      <PinnedSection closeSideMenu={closeSideMenu} />
      <Footer
        isMinimized={isMinimized}
        doMinimize={() => setIsMinimized(!isMinimized)}
      />
    </nav>
  );
};
