import * as React from "react";
import css from "./Animations.module.scss";
import {useAnimationsUtils} from "../../../hooks/useAnimationsUtils";
import {CSSTransition} from "react-transition-group";
interface OpenNodeSettingsAnimationProps {
  isOpened: boolean;
}

export const OpenNodeSettingsAnimation: React.FC<OpenNodeSettingsAnimationProps> = ({
  children,
  isOpened
}) => {
  const nodeCardRef = React.useRef<HTMLDivElement>(null);
  const {calcDistanceFromParentTop} = useAnimationsUtils();

  // React.useEffect(() => {
  //   nodePageRef.current!.appendChild(React.createElement("div"));
  // });
  const translateY = {
    transform: `translateY(-${calcDistanceFromParentTop(nodeCardRef)}px)`
  };
  const transitionClassNames = {
    enter: css.enter,
    enterDone: css.enterDone,
    exit: css.exit,
    exitActive: css.exitActive,
    exitDone: css.exitDone
  };

  return (
    <CSSTransition
      timeout={250}
      in={isOpened}
      classNames={transitionClassNames}
    >
      <div
        ref={nodeCardRef}
        className={css.animationBaseState}
        style={isOpened ? translateY : {}}
      >
        <div
          className={
            isOpened ? css.cardSettingsOpenned : css.cardSettingsClosed
          }
        >
          {children}
        </div>
      </div>
    </CSSTransition>
  );
};
