import * as React from "react";
import {App} from "../App/App";
import {DashboardContext} from "../../Providers/ServerApiContext";
import {DashboardServerApi} from "../../services/DashboardServerApi";
import "../fonts.module.scss";
import "../globals.module.scss";
import {DashboardPageNavigationProvider} from "../../Providers/DashboardPageNavigationProvider";
import {DndProvider} from "react-dnd";
import HTML5Backend from "react-dnd-html5-backend";
import {PinnedAppsContextProvider} from "../../Providers/PinnedAppsContext";
import {FocusedAppContextProvider} from "../../Providers/FocusedAppContextProvider";
import {SessionStorageProvider} from "../../Providers/SessionStorageProvider";

export const AppWithProviders = () => {
  const {userSessionId} = window.__DASHBOARD_GLOBALS__;

  return (
    <SessionStorageProvider userSessionId={userSessionId}>
      {/* drag and drop provider */}
      <DndProvider backend={HTML5Backend}>
        <DashboardContext.Provider
          value={{dashboardServerApi: new DashboardServerApi()}}
        >
          <DashboardPageNavigationProvider>
            <PinnedAppsContextProvider>
              <FocusedAppContextProvider>
                <App />
              </FocusedAppContextProvider>
            </PinnedAppsContextProvider>
          </DashboardPageNavigationProvider>
        </DashboardContext.Provider>
      </DndProvider>
    </SessionStorageProvider>
  );
};
