import {useContext} from "react";
import {AnimationsContext} from "../Providers/AnimationsContextProvider";

export const useAnimationsUtils = () => {
  const {nodePageRef} = useContext(AnimationsContext);

  const calcDistanceFromParentTop = (ref: React.RefObject<HTMLDivElement>) => {
    const nodePageTop =
      nodePageRef.current && nodePageRef.current.getClientRects()[0].top + 20;
    const refTop = ref.current && ref.current.getClientRects()[0].top;
    return refTop && nodePageTop && refTop - nodePageTop;
  };

  return {nodePageRef, calcDistanceFromParentTop};
};
