import * as React from "react";
import css from "../SideMenu.module.scss";
import {usePinnedAppsContext} from "../../../hooks/usePinnedApps";
import {UUID} from "../../../types/internal-types";
import classNames from "classnames";
import {Thumbnail} from "../../Thumbnail/Thumbnail";
import {useFocusedApplication} from "../../../hooks/useFocusedApplication";
import {useServerApiContext} from "../../../hooks/useServerApiContext";
import {useDashboardPageNavigation} from "../../../hooks/useDashboardPageNavigation";
import {DashboardPages} from "../../../enums";

export const PinnedSection: React.FC<{closeSideMenu: () => void}> = ({closeSideMenu}) => {
  const {pinnedAppsList} = usePinnedAppsContext();
  return (
    <section className={css.pinnedSectionWrapper}>
      <div className={css.sideMenuTitle}>Pinned</div>
      {pinnedAppsList.length === 0 ? (
        <div className={css.pinnedEmptyState}>
          <div className={css.explanation}>
            Here you can store the most commonly used Applications. Head to The
            My Applications page and click on the thumbtack.
          </div>
        </div>
      ) : (
        pinnedAppsList.map(({name, uuid}) => (
          <PinnedAppItem uuid={uuid} name={name} key={uuid} closeSideMenu={closeSideMenu}/>
        ))
      )}
    </section>
  );
};

const PinnedAppItem: React.FC<{uuid: UUID; name: string; closeSideMenu: () => void}> = ({uuid, name, closeSideMenu}) => {
  const ref = React.useRef<HTMLDivElement>(null);
  const {setCurrentFocusedApp, currentAppName} = useFocusedApplication();
  const [isLoading, setIsLoading] = React.useState(false);
  const {selectDashboardPage} = useDashboardPageNavigation();
  const {
    dashboardServerApi: {getAppData}
  } = useServerApiContext();

  const openPinnedApp = async (event: React.MouseEvent) => {
    if (event.target !== (ref.current && ref.current.firstChild)) {
      setIsLoading(true);
      const {
        html: [htmlStream],
        scripts: [scriptStream]
      } = await getAppData(uuid);
      setCurrentFocusedApp({
        html: htmlStream,
        script: scriptStream,
        currentAppName: name,
        currentAppId: uuid
      });
      setIsLoading(false);
      selectDashboardPage(DashboardPages.FocusedApp);
      closeSideMenu();
    }
  };

  return (
    <div
      onClick={openPinnedApp}
      className={classNames(css.sideMenuItem, css.pinnedApp, {
        [css.selected]: currentAppName === name
      })}
    >
      <div className={css.pinnedContentWrapper}>
        <Thumbnail name={name} size="x1" />
        <span className={classNames(css.pinnedAppText)}>
          {isLoading ? "loading..." : name}
        </span>
      </div>
    </div>
  );
};
