import React from "react";
import css from "./FocusedApp.module.scss";
import {useFocusedApplication} from "../../hooks/useFocusedApplication";
import {usePinnedAppsContext} from "../../hooks/usePinnedApps";
import {PinnAppIcon} from "../PinnApp/PinnApp";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTimes} from "@fortawesome/free-solid-svg-icons";
import {DashboardPages} from "../../enums";
import {useDashboardPageNavigation} from "../../hooks/useDashboardPageNavigation";
import {PageTitleAction} from "../PageTitle/PageTitleAction";

export const FocusedAppActions: React.FC = () => {
  const {selectDashboardPage} = useDashboardPageNavigation();
  const {pinnApp, getPinnedApp, removeApp} = usePinnedAppsContext();
  const {
    currentAppName,
    currentAppId,
    removeFromFocus
  } = useFocusedApplication();
  const isPinned = !!getPinnedApp(currentAppId);

  React.useEffect(() => {
    return () => removeFromFocus();
    // eslint-disable-next-line
  }, []);

  const closeFocusedApp = () => {
    selectDashboardPage(DashboardPages.ApplicationPage);
  };

  const togglePinnedApp = () =>
    isPinned
      ? removeApp(currentAppId)
      : pinnApp({name: currentAppName, uuid: currentAppId});

  return (
    <>
      <PageTitleAction
        icon={
          <PinnAppIcon isPinned={isPinned} externalstyles={css.pinnAppIcon} />
        }
        doAction={togglePinnedApp}
        text={isPinned ? "UNPIN" : "PIN"}
      />
      <PageTitleAction
        icon={<FontAwesomeIcon icon={faTimes} className={css.faTimes} />}
        doAction={closeFocusedApp}
        text="CLOSE"
      />
    </>
  );
};
