import React from "react";
import css from "./PinnApp.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import {faThumbtack} from "@fortawesome/free-solid-svg-icons";

interface PinnAppIconProps {
  doPinnApp?: () => void;
  isPinned: boolean;
  externalstyles?: string;
}
export const PinnAppIcon: React.FC<PinnAppIconProps> = ({
  doPinnApp,
  isPinned,
  externalstyles
}) => {
  return (
    <span onClick={doPinnApp ? doPinnApp : () => {}}>
      <FontAwesomeIcon
        className={classNames(
          css.pinnAppWrapper,
          css.pinnedIcon,
          externalstyles,
          {
            [css.pinned]: isPinned
          }
        )}
        icon={faThumbtack}
        size="1x"
      />
    </span>
  );
};
